.meal {
	display: flex;
	justify-content: space-between;
	margin: 1rem;
	padding-bottom: 1rem;
	border-bottom: 1px solid #ccc;
}

.meal h3 {
	margin: 0 0 0.25rem 0;
}

.description {
	font-style: italic;
}

.price {
	margin-top: 0.25rem;
	font-weight: bold;
	color: #ad5502;
	font-size: 1.25rem;
}
.button {
	font: inherit;
	font-weight: bold;
	font-size: 1.25rem;
	color: #8a2b06;
	border: 1px solid #8a2b06;
	width: 3rem;
	text-align: center;
	border-radius: 6px;
	background-color: transparent;
	cursor: pointer;
	margin-left: 1rem;
	margin: 0.25rem;
}

.button:hover,
.button:active {
	background-color: #8a2b06;
	color: white;
}
